<template>
	<view :class="!info.status?'verify':''" class="invite-item">
		<view :class="info.sort?'active':''" class="user">
			<image class="top" mode="aspectFill" src="../../static/img/help/top.png" v-if="info.sort"></image>
			<image class="face" mode="aspectFill" :src="info.head_img||logo"></image>
			<text class="name">{{info.nickname||'小松鼠'}}</text>
			<text class="time" v-if="info.sort">{{'置顶至'+info.top_time}}</text>
		</view>
		<view class="invite-content">
			<view class="text">
				<view @click="toLink(info.pub_id,info.project_name)" class="active" v-if="showName">
					<image class="p" src="../../static/img/proj/p.png"></image>{{'#'+info.project_name+'#'}}
				</view>
				<text>{{info.content}}</text>
			</view>
			<view class="img-list">
				<image @click="previewImage(0)" class="img" mode="aspectFill" :src="info.qrcode_group"
					v-if="info.qrcode_group"></image>
				<image @click="previewImage(1)" class="img" mode="aspectFill" :src="info.qrcode_user"
					v-if="info.qrcode_user"></image>
				<image @click="previewImage(i + 2)" class="img" mode="aspectFill" :src="img"
					v-for="(img,i) in info.imgs" :key="i"></image>
			</view>
			<view class="oper" v-if="info.is_edit">
				<image @click="operFn" class="oper-but" src="../../static/img/oper.png"></image>
				<view :class="show_oper?'active':''" class="oper-list">
					<view @click="toLink($root.g0)" class="oper-item" 编辑</view>
					</view>
				</view>
			</view>
		</view>

</template>

<script>
	export default {
		props: {
			info: Object,
			index: Number,
			showName: {
				type: Boolean,
				default: true
			}
		},
		data() {
			return {
				logo: "../../static/img/logo_.png",
				show_oper: false,
				class_list: ["one", "two", "three", "four"]
			}
		},
		methods: {
			donwLoadApp: function() {
				this.$emit("donwLoadApp");
			},
			previewImage: function(n) {
				var e;
				if(this.info.qrcode_group && this.info.qrcode_user){
					e = [this.info.qrcode_group, this.info.qrcode_user]
				}else if(!this.info.qrcode_group && this.info.qrcode_user){
					e = [this.info.qrcode_user]
				}else if(!this.info.qrcode_user && this.info.qrcode_group){
					e = [this.info.qrcode_group]
				}else{
					e = []
				}
				var t = this.info.imgs
				for (var i = 0; i < t.length; i++)
					e.push(t[i]);
					uni.previewImage({
						urls: e,
						current: e[n]
					});
			},
			operFn: function() {
				this.show_oper = !this.show_oper;
			},
			deleFn: function(n, e) {
				console.log(n);
				var t = this;
				t.del_info = {
					id: n,
					index: e
				}, o.showModal({
					title: "",
					content: "删除后不可恢复,是否确认删除？",
					success: function(o) {
						o.confirm ? (console.log("点击了确认"), t.$emit("del", n, e)) : console.log("点击了取消");
					}
				});
			}
		}
	}
</script>

<style lang="scss">
	.invite-item {
		width: 100%;
		position: relative;
		margin-top: 16rpx;
		background: #fff;
		border-radius: 10rpx;
		overflow: hidden;
	}

	.invite-item.verify::after {
		content: "";
		width: 150rpx;
		height: 150rpx;
		display: block;
		position: absolute;
		top: 40rpx;
		right: 0rpx;
		background: url(data:image/png;base64,) no-repeat 50%;
		background-size: cover;
		z-index: 1;
	}

	.invite-item .user {
		display: flex;
		-webkit-box-align: center;
		align-items: center;
		padding: 0 30rpx;
		height: 80rpx;
		position: relative;
	}

	.invite-item .user.active {
		padding: 0 30rpx 0 64rpx;
		background: linear-gradient(131deg, #ff6a46, #ffb259);
	}

	.invite-item .user.active .name,
	.invite-item .user.active .time {
		color: #fff;
	}

	.invite-item .user .face {
		width: 72rpx;
		height: 72rpx;
		border-radius: 100rpx;
		display: block;
		border: 1rpx solid #fff;
		position: relative;
	}

	.invite-item .user .name {
		font-size: 28rpx;
		color: #000;
		padding-left: 10rpx;
		-webkit-box-flex: 1;
		flex: 1;
		width: 1rpx;
	}

	.invite-item .user .time {
		font-size: 22rpx;
		color: #000;
	}

	.invite-item .user .top {
		position: absolute;
		width: 100rpx;
		height: 80rpx;
		top: 0;
		left: 0;
	}

	.invite-item .invite-content {
		padding: 0 24rpx 20rpx;
	}

	.invite-item .invite-content .text {
		line-height: 42rpx;
		font-size: 32rpx;
		color: #353535;
		padding: 20rpx 0;
		word-break: break-all;
		display: block;
	}

	.invite-item .invite-content .text .active {
		font-size: 32rpx;
		color: #307cf8;
		font-weight: 700;
		position: relative;
		background: #f8f8f8;
		display: inline-block;
		padding: 0 10rpx 0 46rpx;
		line-height: 42rpx;
		border-radius: 6rpx;
	}

	.invite-item .invite-content .text .active .p {
		width: 31rpx;
		height: 36rpx;
		position: absolute;
		top: 50%;
		left: 10rpx;
		transform: translateY(-50%);
	}

	.invite-item .img-list {
		width: 100%;
		height: auto;
		display: flex;
		flex-wrap: wrap;
		max-height: 405rpx;
	}

	.invite-item .img-list.active {
		max-height: 190rpx;
	}

	.invite-item .img-list .img {
		width: 190rpx;
		height: 190rpx;
		margin-right: 25rpx;
		margin-bottom: 25rpx;
	}

	.invite-item .img-list .img:nth-child(3n) {
		margin-right: 0;
	}

	.invite-item .oper {
		display: flex;
		-webkit-box-align: center;
		align-items: center;
		height: 70rpx;
		padding-top: 10rpx;
	}

	.invite-item .oper .oper-but {
		width: 50rpx;
		height: 30rpx;
		margin-right: 15rpx;
	}

	.invite-item .oper .oper-list {
		width: 0rpx;
		height: 60rpx;
		overflow: hidden;
		background: rgba(0, 0, 0, .5);
		border-radius: 6rpx;
		color: #fff;
		font-size: 28rpx;
		line-height: 60rpx;
		transition: all .3s;
		display: flex;
	}

	.invite-item .oper .oper-list.active {
		width: 140rpx;
	}

	.invite-item .oper .oper-list .oper-item {
		-webkit-box-flex: 1;
		flex: 1;
		width: 1rpx;
		display: inline-block;
		text-align: center;
		position: relative;
		padding: 0;
	}

	.invite-item .oper .oper-list .oper-item::before {
		content: "";
		width: 1rpx;
		height: 40rpx;
		position: absolute;
		display: block;
		right: 0;
		top: 50%;
		background: #fff;
		transform: translateY(-50%);
	}

	.invite-item .oper .oper-list .oper-item:last-child::before {
		width: 0;
	}
</style>
